h1,h2,h3,h4,h5,h6{font-family:'Permanent Marker', cursive}html,body,div,span,object,iframe,p,blockquote,pre,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,small,strong,sub,sup,var,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,figcaption,figure,footer,header,hgroup,menu,nav,section,summary,time,mark,audio,video{font-family:'Roboto', sans-serif}.application{text-align:center}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.application-header{background-color:#c0362c;width:100%;height:90px;padding:0;border-top:2px solid #001429;border-bottom:4px solid #001429;position:relative;box-shadow:inset 0px 0px 100px -90px #001429}.application-header:before{content:" ";position:absolute;z-index:-1;top:0;left:-5px;right:-5px;bottom:-5px;border:1px solid #ff9;box-shadow:0px 1px 20px 0px #001429}.header-wordmark{position:absolute;left:10%;top:0;bottom:0;right:auto;display:flex;flex-direction:row;align-items:baseline;justify-content:flex-start;height:100%;width:45%}.header-wordmark>h1{color:#001429;text-shadow:-1px 0px 0px #ff9,0 1px 0px #ff9,1px 0 0px #ff9,1px 1px 1px #ff9;paint-order:stroke fill;width:65%;font-size:60px;padding:0;margin:0;text-align:left;vertical-align:middle}.header-wordmark>p{width:35%;font-size:20px;font-weight:700;font-style:italic;color:#001429;opacity:1;padding:0;margin:0;text-align:left;vertical-align:middle}#gg-overlay{height:97%;width:155px;position:absolute;right:auto;left:-12px;top:10;bottom:0;z-index:10;background-size:100%;background-repeat:no-repeat;opacity:0.2}#root{overflow:hidden;width:100%;min-height:800px;height:100%}

